import { Component, OnInit } from '@angular/core';
import {Coupons} from '../../models/coupons';
import {CustomerService} from '../../services/customer.service';

@Component({
  selector: 'app-delete-coupon-purchase',
  templateUrl: './delete-coupon-purchase.component.html',
  styleUrls: ['./delete-coupon-purchase.component.css']
})
export class DeleteCouponPurchaseComponent implements OnInit {

  public coupon: Coupons = new Coupons();

  constructor(private client: CustomerService) {
  }

  public deleteCouponPurchase() {
    this.client.deleteCouponPurchase(this.coupon).subscribe(() => {
      alert('coupon deleted');
    });
  }

  ngOnInit() {
  }

}
